.form-js {
  background-color: #faebd7;
  border-radius: 20px;
  outline: 1px solid gray;
  flex-direction: column;
  gap: 20px;
  width: 400px;
  margin: 0 auto;
  padding: 40px;
  display: flex;
}

.input-name, .input-surname, .input-email, .input-tel {
  border: none;
  border-radius: 5px;
  outline: 1px solid gray;
  width: 100%;
  height: 40px;
  padding-left: 20px;
}

.btn-submit, .btn-unsubmit {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  width: 200px;
  height: 50px;
  margin: 0 auto;
  font-size: 20px;
  display: block;
}

.contact-list {
  width: 400px;
  margin: 20px auto;
  padding: 0;
  list-style: none;
}

.contact-list li {
  background-color: #faebd7;
  border-radius: 15px;
  outline: 1px solid gray;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 15px 20px;
  font-size: 16px;
  display: flex;
}

.contact-list span {
  color: #333;
  flex: 1;
}

.contact-list button {
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 6px 12px;
  font-size: 14px;
}
